/* конец bootstrap4 grid */
.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

.clearfix::before,
.clearfix::after,
.container::before,
.container::after,
.container-fluid::before,
.container-fluid::after,
.row::before,
.row::after {
  content: " ";
  display: table;
}

.clearfix::after,
.container::after,
.container-fluid::after,
.row::after {
  clear: both;
}
