.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: 0;
  clip: rect(0, 0, 0, 0);
}

.sr-only-focusable:active,
.sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto;
}

::placeholder {
  color: #202020;
  opacity: 1;
}

:input-placeholder {
  color: #202020;
  opacity: 1;
}

::placeholder {
  color: #202020;
  opacity: 1;
}

::input-placeholder {
  color: #202020;
  opacity: 1;
}

::selection {
  color: #ffffff;
  background-color: orange;
}

::selection {
  color: #ffffff;
  background-color: orange;
}

@font-face {
  font-weight: 400;
  font-family: "Montserrat-Regular";
  font-style: normal;
  font-display: fallback;
  src: url("../fonts/Montserrat/Montserrat-Regular.eot?#iefix") format("embedded-opentype"), url("../fonts/Montserrat/Montserrat-Regular.woff") format("woff"), url("../fonts/Montserrat/Montserrat-Regular.ttf") format("truetype");
}
@font-face {
  font-weight: 700;
  font-family: "Rubik";
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Rubik/rubik-bold.eot?#iefix") format("embedded-opentype"), url("../fonts/Rubik/rubik-bold.woff2") format("woff2"), url("../fonts/Rubik/rubik-bold.woff") format("woff"), url("../fonts/Rubik/rubik-bold.ttf") format("truetype");
}
html {
  width: 100%;
  margin: 0;
  padding: 0;
  font-size: 16px;
  line-height: 1.42;
  background-color: #fdf9ff;
  backface-visibility: hidden;
  text-size-adjust: 100%;
}

body {
  position: relative;
  min-width: 320px;
  overflow-x: hidden;
  color: #000000;
  font-size: 16px;
  font-family: "Montserrat-Regular", sans-serif;
  line-height: 1.42;
  opacity: 1;
}

body input,
body textarea {
  border: 1px solid #666666;
  outline: 0;
}

body input:required:valid,
body textarea:required:valid {
  border-color: green;
}

body input:focus:required:invalid,
body textarea:focus:required:invalid {
  border-color: red;
}

.img-responsive {
  display: block;
  max-width: 100%;
  height: auto;
}

.img-responsive.img-width {
  width: 100%;
}

input,
select,
textarea {
  font-family: "Montserrat-Regular", sans-serif;
}

.container {
  width: 100%;
  margin: auto;
  padding-right: 30px;
  padding-left: 30px;
}

.aside_left {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  display: none;
  flex-wrap: wrap;
  width: 270px;
  height: 100%;
  padding: 30px 20px;
  overflow-y: auto;
  background-color: #fdf9ff;
  border-right: 1px solid #e8dfec;
  transition: 0.3s;
}

.section {
  padding: 0 30px;
  overflow-y: auto;
  background-color: #f2f2fc;
  opacity: 1;
  transition: 0.3s;
}

.about.section {
  background-color: #fff;
}

.section.header_fon {
  min-height: 70vh;
}

.section.back-section {
  z-index: 1;
}

@keyframes slideSection {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
@keyframes slideSection {
  0% {
    transform: translateX(100%);
  }
  100% {
    transform: translateX(0);
  }
}
.logo {
  padding: 0;
}

.logo a {
  position: relative;
  display: inline-block;
  padding: 0 10px;
  color: #302e4d;
  font-weight: 700;
  font-size: 40px;
  font-family: "Rubik", sans-serif;
  text-decoration: none;
}

.logo a::after,
.logo a::before {
  position: absolute;
  width: 20px;
  height: 20px;
  content: "";
}

.logo a::before {
  top: 0;
  left: 0;
}

.logo a::after {
  right: 0;
  bottom: 0;
}

.aside_left .nav {
  margin: 80px 0 0;
  list-style: none;
}

.aside_left .nav li {
  display: block;
}

.aside_left .nav li a {
  display: block;
  color: #302e4d;
  font-weight: 600;
  font-size: 16px;
  line-height: 45px;
  text-decoration: none;
  border-bottom: 1px solid #e8dfec;
  transition: 0.3s;
}

.aside_left .nav li a:not(.active):hover {
  padding-left: 5px;
}

.copyright_text {
  margin-top: auto;
}

.copyright_text,
.copyright_text ul li a {
  color: #7d7d7d;
  font-size: 13px;
  transition: 0.3s;
}

.copyright_text ul li a:hover {
  text-decoration: none;
}

.copyright_text ul {
  padding: 0;
  list-style: none;
}

.aside_left .nav li a .fa {
  margin-right: 5px;
}

.aside_left .nav-toggler {
  position: fixed;
  top: 30px;
  left: 300px;
  z-index: 11;
  display: none;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background-color: #fdf9ff;
  border: 1px solid #d4d4e3;
  cursor: pointer;
  transition: 0.3s;
}

.aside_left .nav-toggler span {
  position: relative;
  display: inline-block;
  width: 16px;
  height: 2px;
}

.aside_left .nav-toggler.open span {
  background-color: transparent;
