.btn_calc {
  margin-top: 20px;
}

.change__thene {
  display: flex;
  justify-content: flex-start;
}

.change__thene_item {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.body-skin {
  position: relative;
  margin: 0 5px;
  border-radius: 50%;
  cursor: pointer;
}

.body-skin:first-child {
  margin-left: 25px;
}

.body-skin::before {
  position: absolute;
  top: 0;
  left: -24px;
  width: 20px;
  height: 20px;
  border: 1px solid #d4d4e3;
  content: '';
}

.body-skyn__dark::before {
  background-color: #151515;
}

.body-skyn__light::before {
  background-color: #fdf9ff;
}

.decoration_scene {
  display: flex;
  justify-content: flex-start;
  margin-top: 8px;
  margin-bottom: 5px;
}

.scene__items {
  width: 20px;
  height: 20px;
  margin: 0 5px;
  cursor: pointer;
}

